.nav.is-top {
    .nav__content li a {
        transition: color 0.3s;
        color: $white;
    }
}
.nav.is-active {
    .nav__content {
        top: 0;

        li a {
            color: $black;

            &:hover {
                color: $black;
            }
        }
    }
}

.nav__content {
    position: fixed;
    left: 0;
    top: -120%;
    z-index: 10;
    transition: all 0.3s;
    height: 100%;
    width: 100%;
    background-color: $white;

    @media (min-width: $screen-sm-min) {
        top: 50px;
        background-color: transparent;
        z-index: 10;
        width: 100%;
        height: auto;
        min-width: $content-width-large;
    }

    ul {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        @media (min-width: $screen-sm-min) {
            display: flex;
            justify-content: center;
            position: static;
            transform: translate(0,0);
        }
    }

    li {
        margin-top: 20px;
        text-align: center;
        @include font-settings(
            $font-size: 20px,
            $line-height: px(20 * 1.4),
            $font-weight: bold,
            $letter-spacing: 0.15em,
            $font-family: $base-font-family-en
        );

        @media (min-width: $screen-sm-min) {
            margin-left: 27px;
            margin-right: 27px;
            margin-top: 0;
            position: relative;
            @include font-settings(
            $font-size: 16px,
            $line-height: 22px,
            $font-weight: 600,
            $letter-spacing: 0.15em,
            $font-family: $base-font-family-en
            );
        }

        &:first-child {
            margin-top: 0;
        }
        a {
            display: block;
            color: $black;

            &:after {
                @media (min-width: $screen-sm-min) {
                    content: '';
                    display: block;
                    width: 100%;
                    height: 2px;
                    margin-top: 20px;
                    transition: background-color 0.3s;
                }
            }

            &:hover {
                &:after {
                    background-color: $yellow;
                }
            }

            img {
                vertical-align: initial;
            }
        }
    }
}

.nav--entry-comp {
    @media (min-width: $screen-sm-min) {
        position: relative;
        .nav__content {
            position: static;
            ul {
                justify-content: flex-end;
                align-items: center;
                padding-right: px(60 - 27);
                padding-top: 55px;
                padding-bottom: px(55 - 20);
                width: 100%;
            }
        }
        li {
            a {
                color: $black;
            }
        }

        .nav__menu-logo {
            display: block;
            position: absolute;
            top: 50%;
            left: 40px;
            margin-right: 0;
            margin-left: 0;

            transform: translateY(-50%);

            img {
                width: 70px;
            }
            a {
                &:after {
                    display: none;
                }
            }
        }
    }
}

.nav__toggle {
    position: fixed;
    top: 10px;
    right: 5px;
    z-index: 10;
    transition: all 0.3s;
    display: inline-block;

    @media (min-width: $screen-sm-min) {
        position: static;
        display: none;
    }
}

.nav__menu-mail {
    @media (min-width: $screen-sm-min) {
        display: none;
    }
}

.nav__menu-logo {
    display: none;
}
