.curtain {
    opacity: 1;
    transition: opacity 1s;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100vh;
    min-height: px(830/2);

    @media (min-width: $screen-sm-min) {
        min-height: 770px;
    }
}
.curtain__bg {
    display: block;
    background: $yellow;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 300;
    margin: auto;
    opacity: 1;
    transition: opacity 3.5s;
}

.curtain__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 301;
    transform: translate(-50%, -50%);
    margin-top: -78px;
    opacity: 0;
    transition: all 0.5s;
    width: 235px;

    object {
        width: 100%;
    }

    @media (min-width: $screen-sm-min) {
        margin-top: -74px;
        width: 456px;
    }
}

