.button {
    display: block;
    width: 100%;
    border-radius: 999px;
    text-align: center;
    background-color: $yellow;
    padding-top:24px;
    padding-bottom:24px;
    transition: all 0.3s;
    color: $black;
    cursor: pointer;
    &:link, &:visited {
        color: $black;
    }
    @include font-settings(
        $font-size: px(28/2),
        $line-height: px(28/2),
        $font-weight: bold,
        $letter-spacing: 0.1em,
        $font-family: $base-font-family-en
    );

    @media (min-width: $screen-sm-min) {
        &:hover {
            background-color: $black;
            color: $white;
            &:link, &:visited {
                color: $white;
            }
        }
        @include font-settings(
            $font-size: 17px,
            $line-height: 22px,
            $font-weight: bold,
            $letter-spacing: 0.1em,
            $font-family: $base-font-family-en
        );
    }
}

.button--black {
    background-color: $black;
    font-weight: 600;
    color: $white;
    &:link, &:visited {
        color: $white;
    }

    @media (min-width: $screen-sm-min) {
        &:hover {
            background-color: $yellow;
            color: $black;
            &:link, &:visited {
                color: $black;
            }
        }
    }
}

