.header {
    position: relative;
}

.header__slider {
    min-height: px(830/2);

    @media (min-width: $screen-sm-min) {
        min-height: 770px;
    }
}

.header__content {
    position: absolute;
    z-index: 3;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    min-height: px(830/2);

    @media (min-width: $screen-sm-min) {
        min-height: 770px;
    }
}

.header__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    max-width: 600px;
    text-align: center;
    margin-top: -34px;
    margin-left: 1px;
    transform: translate(-50%, -50%);
    img {
        max-width: 150px;
    }
    > hr {
        width: 100px;
        border-bottom: 1px solid $white;
        margin-left: auto;
        margin-right: auto;
        margin-top: 20px;
        margin-bottom: 20px;
    }

    @media (min-width: $screen-sm-min) {
        img {
            max-width: 290px;
        }
        > hr {
            width: 200px;
            margin-top: 30px;
            margin-bottom: 30px;
        }
    }
}

.header__logo--sp {
    position: fixed;
    top: 20px;
    left: 15px;
    z-index: 3;

    @media (min-width: $screen-sm-min) {
        display: none;
    }

    img {
        width: 141px;
        height: auto;
    }
}

.header__text {
    text-align: center;
    color: $white;

    @include font-settings(
        $font-size: px(34/2),
        $line-height: px(52/2),
        $font-weight: bold,
        $letter-spacing: 0.08em
    );

    @media (min-width: $screen-sm-min) {
        br {
            display: none;
        }
        @include font-settings(
            $font-size: 24px,
            $line-height: 24px,
            $letter-spacing: 0.12em
        );
    }
}

.header__scrolldown {
    position: absolute;
    bottom: 45px;
    left: 50%;
    z-index: 5;
    transform: translateX(-50%);
    color: $white;
    cursor: pointer;
    text-align: center;
    @include font-settings(
        $font-size: px(18/2),
        $line-height: px(34/2),
        $letter-spacing: 0.15em,
        $font-weight: bold,
        $font-family: $base-font-family-en
    );

    img {
        margin-top: px(24/2);
        width: px(56/2);
    }

    @media (min-width: $screen-sm-min) {
        bottom: 80px;
        @include font-settings(
            $font-size: 13px,
            $line-height: 13px,
            $letter-spacing: 0.15em,
            $font-family: $base-font-family-en
        );

        img {
            margin-top: 15px;
        }
    }
}
