.content {
    padding-left: $gutter;
    padding-right: $gutter;
    background-image: url(../images/sripe-pattern.png);
    background-size: 6px;
    p {
        margin-top: 20px;
        > br {
            display: none;
        }
    }

    @media (min-width: $screen-sm-min) {
        width: $content-width-large;
        margin-right: auto;
        margin-left: auto;
        padding-left: 0;
        padding-right: 0;
        background-image: none;

        p {
            margin-top: 30px;
            > br {
                display: initial;
            }
        }
    }
}

.content--entry-comp {
    > *:last-child {
        padding-bottom: 30px;
    }
    @media (min-width: $screen-sm-min) {
        .content__body {
            p {
                text-align: center;
            }
        }
        .content__button {
            width: 400px;
        }
        > *:last-child {
            padding-bottom: 100px;
        }
    }
}

.content--message, .content--vision {
    .content__body {
        > *:last-child {
            margin-bottom: 40px;
        }
    }
}

%content--collapse-base {
    @media (min-width: $screen-sm-min) {
        .content__body {
            max-width: 940px;
            > *:last-child {
                margin-bottom: 0;
            }
        }
        .content__header {
            margin-top:-148px;
            max-width: 640px;
            position: relative;
            z-index: 1;
        }
        .content__body {
            margin-top: 175px;
            padding-left: 70px;
            padding-right: 70px;
            padding-bottom: 70px;
            background-image: url(../images/sripe-pattern.png);
            background-size: 6px;
        }
    }
}

.content--collapse {
    @extend %content--collapse-base;

    @media (min-width: $screen-sm-min) {
        .content__header {
            float: right;
            margin-bottom: 20px;
            margin-left: 20px;
        }

        .content__body {
            padding-left: 140px;
        }
    }
}

.content--collapse-flip {
    @extend %content--collapse-base;

    @media (min-width: $screen-sm-min) {
        .content__header {
            float: left;
            margin-bottom: 20px;
            margin-right: 40px;
        }

        .content__body {
            padding-right: 140px;
            margin-left: auto;
            margin-right: 0;
        }
    }
}

.content--recruit {
    .content__body {
        > *:last-child {
            margin-bottom: 80px;
        }
    }
    @media (min-width: $screen-sm-min) {
        max-width: 100%;
        min-width: $content-width-large;
        position: relative;
        .content__body {
            > *:last-child {
                margin-bottom: 0;
            }
        }
        .content__header {
            float: right;
            max-width: 760px;
            margin-top: -(165px - 45px);
            position: relative;
            z-index: 1;
            + * {
                clear: both;
            }
        }
        .content__job {
            margin-top: -(165px - 45px - 40px);
            position: absolute;
            left: 140px;
            top: 0;
            z-index: 1;
        }
    }
}

.content--company {
    @media (min-width: $screen-sm-min) {
        background-image: none;
    }
}

.content--entry {
    @media (min-width: $screen-sm-min) {
        background-image: none;
        width: $content-width;

        .content__button {
            max-width: 360px;
        }
    }
}

.content--contact {
    background-image: none;
    padding-bottom: 30px;

    @media (min-width: $screen-sm-min) {
        .content__button {
            max-width: 500px;
        }

        padding-bottom: 100px;
    }
}

.content--job {
    .content__body {
        > *:last-child {
            margin-bottom: 40px;
        }
    }
}

.content__header {
    margin-left: -($gutter);
    margin-right: -($gutter);
    > img {
        width: 100%;
        vertical-align: middle;
        box-shadow: 6px 6px 12px rgba(0,0,0,0.18);
    }

    @media (min-width: $screen-sm-min) {
        margin-right: 0;
        margin-left: 0;
    }
}

.content__body {
    border-top: solid 1px transparent;
    border-bottom: solid 1px transparent;
}

.content__copy {
    margin-top: 30px;
    margin-bottom: 25px;

    @media (min-width: $screen-sm-min) {
        margin-top: 40px;
    }
}

.content__sign {
    text-align: right;
    margin-top: 25px;
    line-height: 1;
    > div {
        margin-top: 10px;
    }
}

.content__job {
    margin-top: 30px;

    @media (min-width: $screen-sm-min) {
        margin-top: 0;
        width: 340px;
    }
}

.content__company {
    margin-top: 30px;

    @media (min-width: $screen-sm-min) {
        max-width: $content-width;
        margin-right: auto;
        margin-left: auto;
    }
}

.content__image-grally {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;

    > li {
        display: block;
        width: 50%;

        @media (min-width: $screen-sm-min) {
            margin-bottom: 70px;
        }

        img {
            width: 100%;
            height: auto;
        }
    }
}

.content__image-grally--company {
    margin-top: 15px;
    box-shadow: 6px 6px 12px rgba(0,0,0,0.18);

    @media (min-width: $screen-sm-min) {
        box-shadow: none;
        margin-top: 40px;
        margin-left: auto;
        margin-right: auto;
        max-width: 1090px;
        display: flex;
        > li {
            width: 33.3333%;
            padding-right: 10px;
            padding-left: 10px;
            img {
                box-shadow: 6px 6px 12px rgba(0,0,0,0.18);
            }
            &:first-child {
                width: 33.3333%;
            }
        }
    }
}

.content__guidline {
    position: relative;
    table {
        margin-top: px(60/2);
    }

    @media (min-width: $screen-sm-min) {
        table {
            max-width: 1000px;
            margin-right: auto;
            margin-left: auto;
            margin-top: 70px;
        }
    }
}

.content__guidline--first {
    @media (min-width: $screen-sm-min) {
        margin-top: 110px;
    }
}

%content__image-grally--collapse-base {
    @media (min-width: $screen-sm-min) {
        box-shadow: none;
        margin-right: auto;
        margin-left: auto;
        position: absolute;
        width: (1040px + 10px*2);
        left: 110px;
        > li {
            margin-left: percentage(10 / 1060);
            margin-right: percentage(10 / 1060);
            img {
                box-shadow: 6px 6px 12px rgba(0,0,0,0.18);
            }
            width: percentage(260 / 1060);
            &:first-child {
                width: percentage(480 / 1060);
            }
        }
    }
}

.content__image-grally--collapse-upper {
    @extend %content__image-grally--collapse-base;
    > li {
        img {
            box-shadow: 6px 6px 12px rgba(0,0,0,0.18);
        }
    }
    @media (min-width: $screen-sm-min) {
        box-shadow: none;
        top: 100px;
    }
}

.content__image-grally--collapse-down {
    @extend %content__image-grally--collapse-base;
    > li {
        img {
            box-shadow: 6px 6px 12px rgba(0,0,0,0.18);
        }
    }
    @media (min-width: $screen-sm-min) {
        align-items: flex-end;
        flex-direction: row-reverse;
        top: (35px + 63px);
    }
}

.content__entry {
    margin-top: px(30/2);

    @media (min-width: $screen-sm-min) {
        margin-top: 40px;
    }
}

.content__button {
    @media (min-width: $screen-sm-min) {
        max-width: 580px;
        margin-left: auto;
        margin-right: auto;
    }
}
