.guidline {
    width: 100%;
    tbody {
        background-color: $white;
    }
    ul, ol {
        list-style: none;
    }
    ul {
        > li {
            display: flex;
            align-items: baseline;
            margin-top: 10px;
            &:before {
                margin-right: 0.5em;
                content: counter(foo) '.';
                color: transparent;
                background-image: url(../images/bullet.svg);
                background-size: 6px;
                background-repeat: no-repeat;
                background-position: center;
            }
            &:first-child {
                margin-top: 0;
            }
        }
    }
    tr {
        display: block;
        padding: 20px;
        border-top: solid 2px $gray;
        &:first-child {
            border-top: none;
        }
    }

    @media (min-width: $screen-sm-min) {
        ul {
            > li {
                &:before {
                    background-size: 8px;
                }
            }
        }

        td {
            display: table-cell;
        }

        tr {
            padding: 25px 35px;
        }
    }
}

.guidline__head {
    display: block;
    br {
        display: none;
    }
    @include font-settings(
        $font-size: px(28/2),
        $line-height: px(28/2),
        $font-weight: bold,
        $letter-spacing: 0.1em
    );

    @media (min-width: $screen-sm-min) {
        width: 175px;
        vertical-align: top;
        @include font-settings(
            $font-size: 15px,
            $line-height: 22px,
            $font-weight: bold,
            $letter-spacing: 0.1em
        );

        br {
            display: initial;
        }
    }
}

.guidline__body:not(:first-child) {
    display: block;
    margin-top: 10px;

    @media (min-width: $screen-sm-min) {
        display: table-cell;
    }
}

.guidline--collapse-upper {
    @media (min-width: $screen-sm-min) {
        tr {
            padding-left: 480px;
            padding-top: (130px + 30px);
        }

        tbody { // for IE11
            display: block;
            width: 1000px;
        }

        .guidline__body {
            ul {
                margin-top: -0.5em;
                > li {
                    float: left; // flexを入れ子にするとIE11で不具合が起きたのでfloatで対応
                    width: 50%;
                    margin-top: 0.5em;
                    padding-left: 30px;
                }
                &:after {
                    content: '';
                    display: block;
                    clear: both;
                }
            }
        }
    }
}

.guidline--collapse-down {
    @media (min-width: $screen-sm-min) {
        margin-bottom: (120px + 90px);
        tr {
            padding-top: 60px;
            padding-bottom: 120px;
        }
    }
}
