.footer {
    text-align: center;
    background-color: $gray;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    img {
        margin-top: px(50/2);
    }
}

.footer__copy {
    background-color: $black;
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: center;
    line-height: 0;
    small {
        color: $white;
        @include font-settings(
            $font-size: px(16/2),
            $line-height: px(16/2),
            $font-weight: 600,
            $letter-spacing: 0.03em,
            $font-family: $base-font-family-en
        );
    }

    @media (min-width: $screen-sm-min) {
        padding-top: 25px;
        padding-bottom: 25px;
        small {
            @include font-settings(
                $font-size: 11px,
                $line-height: 12px,
                $font-weight: 400,
                $letter-spacing: 0.03em,
                $font-family: $base-font-family-en
            );
        }
    }
}

.footer__address {
    margin-top: 15px;
    margin-bottom: 20px;
    p {
        text-align: center;
    }
}

.footer__logo {
    max-width: 150px;

    @media (min-width: $screen-sm-min) {
        max-width: 230px;
    }
}
