.catchcopy {
    text-align: center;
    @include font-settings(
        $font-size: px(34/2),
        $line-height: px(50/2),
        $font-weight: bold,
        $letter-spacing: 0.08em
    );
    br {
        display: none;
    }

    @media (min-width: $screen-sm-min) {
        text-align: left;
        br {
            display: initial;
        }
        @include font-settings(
            $font-size: 28px,
            $line-height: 42px,
            $letter-spacing: 0.08em
        );
    }
}

.catchcopy--entry-comp {
    @media (min-width: $screen-sm-min) {
        text-align: center;
    }
}

