.form {
    input:not([type="radio"]), select, textarea {
        background-color: $white;
        width: 100%;
        padding: ((40-18)/2/13) *1em; // (targetSize - lineHeight)/2/fontSize
        min-width: 0; // for firefox
    }
    select {
        background-image: url(../images/caret.svg);
        background-size: 10px 8px;
        background-position: right 15px center;
        background-repeat: no-repeat;
    }
    input[type="radio"] {
        display: none;
        + label {
            display: inline-block;
            vertical-align: bottom;
            &:before {
                content: '';
                display: inline-block;
                background-image: url(../images/radio.svg);
                background-repeat: no-repeat;
                background-position: center;
                background-size: 100%;
                height: 25px;
                width: 25px;
            }
        }
        &:checked {
            + label {
                &:before {
                    background-image: url(../images/radio-active.svg);
                }
            }
        }
    }

    @media (min-width: $screen-sm-min) {
        input[type="radio"] {
            + label {
                vertical-align: sub;
            }
        }
    }
}

.form__group {
    @media (min-width: $screen-sm-min) {
        position: relative;
        padding-left: 260px;
        margin-top: 25px;
    }
}

.form__group--gender {
    label {
        > * {
            vertical-align: text-bottom;
        }
        span {
            margin-left: 0.65em;
            margin-right: (0.65em * 3);
            @include font-settings(
                $font-size: px(30/2),
                $line-height: px(30/2),
                $font-weight: 400,
                $letter-spacing: 0.01em
            );
        }
    }

    @media (min-width: $screen-sm-min) {
        label {
            span {
                @include font-settings(
                    $font-size: 16px,
                    $line-height: 22px,
                    $font-weight: 400,
                    $letter-spacing: 0.01em
                );
            }
        }
    }
}

.form__title {
    display: block;
    margin-top: px(50/2);
    margin-bottom: 10px;
    @include font-settings(
        $font-size: px(26/2),
        $line-height: px(36/2),
        $font-weight: bold,
        $letter-spacing: 0.01em
    );

    @media (min-width: $screen-sm-min) {
        @include font-settings(
            $font-size: 15px,
            $line-height: 22px,
            $font-weight: bold,
            $letter-spacing: 0.01em
        );

        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        margin-top: 0;
        margin-bottom: 0;
        padding-right: 40px;
        width: 260px;
    }
}

.form__input-group {
    display: flex;
    align-items: center;
    width: 100%;
}

.form__input-group-item {
    width: 33.3333%;
}

.form__hyphen {
    padding-left: 1em;
    padding-right: 1em;
    @include font-settings(
        $font-size: px(30/2),
        $line-height: px(30/2),
        $font-weight: 400,
        $letter-spacing: 0.01em
    );
}

.form__date {
    padding-left: 0.5em;
    padding-right: 1em;
    min-width: 2.5em; // pad-left + pad-right + ownCharWidth(1em)
    &:last-child {
        padding-right: 0;
        min-width: 1.5em;
    }
    @include font-settings(
        $font-size: px(30/2),
        $line-height: px(30/2),
        $font-weight: 400,
        $letter-spacing: 0.01em
    );

    @media (min-width: $screen-sm-min) {
        span {
            @include font-settings(
                $font-size: 16px,
                $line-height: 22px,
                $font-weight: 400,
                $letter-spacing: 0.01em
            );
        }
    }
}

.form__button {
    margin-top: px(50/2);
    margin-bottom: px(80/2);

    @media (min-width: $screen-sm-min) {
        margin-top: 40px;
        margin-bottom: 80px;
    }
}
