.is-top .toggle-button {
    span {
        background-color: $white;
    }
}
.is-active .toggle-button {
    span {
        background-color: $black;
    }
}

.toggle-button {
    display: block;
    padding: 10px;
}

.toggle-button__inner {
    width: 25px;
    height: 23px;
    position: relative;
    span {
        display: block;
        width: 100%;
        height: 2px;
        background-color: $black;
        border-radius: 999px;
        box-shadow: 0 1px 0 rgba($black, 0.1);
        position: absolute;
        left: 0;
        transition: all 0.3s;
        &:nth-child(1) {
            top: 0;
        }
        &:nth-child(2) {
            top: px(8 + 2);
        }
        &:nth-child(3) {
            top: px((8 + 2)*2);
        }
    }
}

.is-active {
    span {
        &:nth-child(1) {
            transform: rotate(135deg);
            top: 10px;
        }
        &:nth-child(2) {
            left: -100%;
            opacity: 0;
        }
        &:nth-child(3) {
            transform: rotate(-135deg);
            top: px((8 + 2)*2 - 10);
        }
    }
}
