// Open Sans
@import 'https://fonts.googleapis.com/css?family=Open+Sans:400,400i,600,600i,700,700i';

html, body {
    overflow-x: hidden;

    @media (min-width: $screen-sm-min) {
        overflow-x: auto;
    }
}

body {
    position: relative;
    min-height: 100vh;
    padding-bottom: 144px;
    @include font-settings(
        $font-size: $base-font-size,
        $line-height: $base-line-height,
        $letter-spacing: $base-letter-spacing
    );
	-webkit-text-size-adjust: 100%;
	text-size-adjust: 100%;
	-webkit-font-smoothing: $font-smoothing;
	font-smoothing: $font-smoothing;
	text-rendering: $text-rendering;
	color: $base-font-color;
	background-color: $base-background-color;

    @media (min-width: $screen-sm-min) {
        padding-bottom: 185px;
        min-width: $content-width-large;
        @include font-settings(
            $font-size: $base-font-size-pc,
            $line-height: $base-line-height-pc,
            $letter-spacing: $base-letter-spacing-pc
        );
    }
}

* {
    box-sizing: border-box;
}

p {
    text-align: justify;
}

a {
    &:hover {
        opacity: 0.85;
    }
}
