/**
 * Base
 *********************** */
* {
  box-sizing: border-box;
}

img {
  vertical-align: bottom;
}

/**
 * vertical-slider
 *********************** */
.vertical-slider {
  height: 100vh;
  background-color: black;
  overflow: hidden;
  position: relative;

  &__nav {
    padding-left: 0;
    list-style: none;
    margin: 0;
    position: absolute;
    z-index: 5;
    right: 15px;
    bottom: 20px;

    @media (min-width: $screen-sm-min) {
      bottom: auto;
      right: 50px;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  &__nav-btn {
    margin-top: 10px;
    line-height: 1;
    &:before {
      content: '';
      display: block;
      height: 8px;
      width: 8px;
      border: 1px solid $white;
      border-radius: 100%;
      transition: background-color 0.3s;
    }

    @media (min-width: $screen-sm-min) {
      margin-top: 25px;
      &:before {
        height: 20px;
        width: 20px;
      }
    }
  }

  &__nav-btn--current {
    &:before {
      background-color: $white;
    }
  }

  &__panel {
    background-color: white;
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: absolute;
    top: -100%;
    left: 0;
    transition: all 0.5s cubic-bezier(1, 0.1, 0, 1);
  }

  &__image {
    overflow: hidden;
    height: 100%;
    div {
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      height: 100%;
      transition: all 7s linear;
    }
  }

  // transition中に付与されるクラス
  &__panel--active {
    top: 0 !important;
    z-index: 2;
    .vertical-slider__image {
      div {
        transform: scale(1.1);
      }
    }
  }

  // transition後のpanelに付与されるクラス
  &__panel--current {
    top: 0 !important;
    z-index: 1;
    .vertical-slider__image {
      div {
        transform: scale(1.1);
      }
    }
  }
}
