.headline {
    display: block;
    text-align: center;
}

.headline--entry-comp {
    overflow-x: hidden;
    .headline__main {
        transform: translateX(0.16em);
    }
}

.headline--collapse {
    @media (min-width: $screen-sm-min) {
        text-align: left;
        max-width: $content-width-large;
        margin-right: auto;
        margin-left: auto;
    }
}

.headline--caption {
    @media (min-width: $screen-sm-min) {
        display: table-caption !important;
    }
}

.headline--caption-collapse {
    @extend .headline--caption;
    @media (min-width: $screen-sm-min) {
        padding-bottom: 100px;
    }
}

.headline--small {
    .headline__main {
        @include font-settings(
            $font-size: px(42/2),
            $line-height: px(42/2),
            $letter-spacing: 0.1em,
            $font-family: $base-font-family
        );
    }
    .headline__sub {
        transform: translateY(0.5em);
        color: $yellow-darker;
        @include font-settings(
            $font-size: px(20/2),
            $line-height: px(20/2),
            $letter-spacing: 0.15em,
            $font-family: $base-font-family-en
        );
    }

    @media (min-width: $screen-sm-min) {
        .headline__main {
            @include font-settings(
                $font-size: 32px,
                $line-height: 32px,
                $letter-spacing: 0.1em
            );
        }

        .headline__sub {
            // transform: translateY(0);
            @include font-settings(
                $font-size: 12px,
                // $line-height: 22px,
                $line-height: 12px,
                $letter-spacing: 0.15em
            );
        }
    }
}

.headline__main {
    display: block;
    @include font-settings(
        $font-size: px(56/2),
        $line-height: px(56/2),
        $letter-spacing: 0.16em,
        $font-family: $base-font-family-en
    );

    @media (min-width: $screen-sm-min) {
        @include font-settings(
            $font-size: 48px,
            $line-height: 48px,
            $letter-spacing: 0.16em
        );
    }
}

.headline__sub {
    display: block;
    @include font-settings(
        $font-size: px(22/2),
        $line-height: px(22/2),
        $font-weight: bold,
        $letter-spacing: 0.08em
    );
    margin-top: px(20/2);

    @media (min-width: $screen-sm-min) {
        @include font-settings(
            $font-size: 16px,
            $line-height: 22px,
            $letter-spacing: 0.08em
        );
    }
}

