@function px($font-size) {
    @return $font-size *1px;
}

@function rem($font-size: $base-font-size) {
    @return ($font-size/$root-font-size) *1rem;
}

@mixin font-settings($font-size, $line-height,
    $letter-spacing, $font-family: null,
    $font-weight: null, $font-style: null) {
    font-size: rem($font-size);
    line-height: ($line-height / $font-size);
    letter-spacing: $letter-spacing;
    font-family: $font-family;
    font-weight: $font-weight;
    font-style: $font-style;
}
