.address__name {
    @include font-settings(
        $font-size: px(24/2),
        $line-height: px(24*1.4/2),
        $font-weight: bold,
        $letter-spacing: 0.03em
    );

    @media (min-width: $screen-sm-min) {
        @include font-settings(
            $font-size: 15px,
            $line-height: 15px,
            $font-weight: bold,
            $letter-spacing: 0.03em
        );
    }
}

.address__zip {
    margin-top: 5px;
    @include font-settings(
        $font-size: px(22/2),
        $line-height: px(22*1.4/2),
        $font-weight: 400,
        $letter-spacing: 0.03em
    );

    @media (min-width: $screen-sm-min) {
        @include font-settings(
            $font-size: 12px,
            $line-height: 12px,
            $font-weight: 400,
            $letter-spacing: 0.03em
        );
    }
}

