.section--recruit {
    @media (min-width: $screen-sm-min) {
        background-image: url(../images/sripe-pattern.png);
        background-size: 6px;

        .section__header {
            padding-bottom: 200px;
            &:after {
                bottom: (200px - 35px);
            }
        }
    }
}

.section--entry {
    background-color: $gray-darker;

    @media (min-width: $screen-sm-min) {
        background-image: url(../images/sripe-pattern.png);
        background-size: 6px;
        .section__header {
            background-image: url(../images/sripe-pattern.png);
            background-size: 6px;
            background-color: none;
        }
    }
}

.section--entry-comp {
    .section__header {
        background-image: url(../images/sripe-pattern.png);
        background-size: 6px;
    }

    @media (min-width: $screen-sm-min) {
        .section__header {
            padding-top: 45px;
            padding-bottom: 45px;
        }
    }
}

.section__header {
    padding-top: px(60/2);
    padding-bottom: px(50/2);
    position: relative;
    background-color: $white;
    &:after {
        content: '';
        position: absolute;
        display: block;
        width:140px;
        height:5px;
        left: 50%;
        bottom: 0;
        background-color: $yellow;
        transform: translate(-50%, 50%);
    }

    @media (min-width: $screen-sm-min) {
        padding-top: 80px;
    }
}

%section--collapse-base {
    @media (min-width: $screen-sm-min) {
        .section__header {
            width: $content-width-large;
            margin-right: auto;
            margin-left: auto;
            padding-top: 50px;
            padding-bottom: 50px;
            transform: translateY(100%);
            margin-top: -(170px - 90px);

            &:after {
                bottom: 0;
                transform: translate(0, 50%);
            }
        }
    }
}

.section--collapse {
    @extend %section--collapse-base;
    @media (min-width: $screen-sm-min) {
        .section__header {
            padding-left: 140px;

            &:after {
                left: 140px;
            }
        }
    }
}

.section--collapse-flip {
    @extend %section--collapse-base;
    @media (min-width: $screen-sm-min) {
        .section__header {
            padding-left: (640px + 50px); // imageWidth + margin
            &:after {
                left: (640px + 50px);
            }
        }
    }
}

