.job__list {
    list-style: none;
}

.job__list-item {
    @include font-settings(
        $font-size: px(28/2),
        $line-height: px(28/2),
        $font-weight: bold,
        $letter-spacing: 0.03em
    );
    background-color: $white;
    box-shadow: 3px 3px rgba(0,0,0,0.1);
    margin-top: 10px;
    padding: 15px;
    img {
        height: 1.2em;
        vertical-align: text-bottom;
        margin-right: 15px;
    }

    &:nth-child(1) {
        background-color: #fbeef6;
        color: #cc4297;
    }

    &:nth-child(2) {
        background-color: #e9ecf8;
        color: #3747a3;
    }

    &:nth-child(3) {
        background-color: #f7e9dd;
        color: #df8238;
    }

    &:nth-child(4) {
        background-color: #edf6e9;
        color: #9fd184;
    }

    @media (min-width: $screen-sm-min) {
        @include font-settings(
            $font-size: 20px,
            $line-height: 22px,
            $font-weight: bold,
            $letter-spacing: 0.03em
        );
        padding: 30px 20px;
        &:first-child {
            margin-top: 0;
        }
    }
}

.job__heading {
    position: relative;
    @include font-settings(
        $font-size: px(42/2),
        $line-height: px(42/2),
        $font-weight: 400,
        $letter-spacing: 0.1em
    );
    &:after {
        content: '';
        display: block;
        position: relative;
        top: -0.5em;
        left: -($gutter);
        height: 1em;
        width: 100%;
        background-color: $yellow-lighter;
    }

    @media (min-width: $screen-sm-min) {
        @include font-settings(
            $font-size: 32px,
            $line-height: 32px,
            $font-weight: 400,
            $letter-spacing: 0.1em
        );
        &:after {
            left: 0;
        }
    }
}

.job__heading-text {
    display: inline-block;
    position: relative;
    z-index: 1;
    transform: skewX(-10deg);

    @media (min-width: $screen-sm-min) {
        margin-left: 20px;
    }
}

.job__heading-subtext {
    display: inline-block;
    position: absolute;
    z-index: 1;
    transform: skewX(-10deg);
    top: 5px;
    color: rgba($white, 0.5);
    right: $gutter + 10px;
    @include font-settings(
        $font-size: px(42/2),
        $line-height: px(42/2),
        $font-weight: 400,
        $font-style: italic,
        $letter-spacing: 0.15em,
        $font-family: $base-font-family-en
    );

    @media (min-width: $screen-sm-min) {
        right: 10px;
        @include font-settings(
            $font-size: 30px,
            $line-height: 30px,
            $font-weight: 400,
            $font-style: italic,
            $letter-spacing: 0.15em,
            $font-family: $base-font-family-en
        );
    }
}
