
//antialiased,subpixel-antialiased,none
$font-smoothing: antialiased;

//auto,optimizeSpeed,optimizeLegibility,geometricPrecision
$text-rendering: auto;

$root-font-family: "游ゴシック", YuGothic, "ヒラギノ角ゴ ProN W3", "Hiragino Kaku Gothic ProN", "メイリオ", Meiryo, sans-serif;
$base-font-family: $root-font-family;
$base-font-family-en: "Open Sans", $base-font-family;
$root-font-size: 10px;
$base-font-size: 13px;
$base-font-size-pc: 15px;
$base-line-height: 18px;
$base-line-height-pc: 26px;
$base-letter-spacing: 0.03em;
$base-letter-spacing-pc: 0.03em;
$base-font-color: #333;
$base-background-color: #fff;

/**
 * generals
 * ***************/
$gutter: 15px;

/**
 * Screen
 * ***************/
$screen-sm-min: 768px;
$screen-sm-max: $screen-sm-min - 1px;
$screen-md-min: 1080px;
$screen-md-max: $screen-md-min - 1px;
$screen-pc-max: 1600px;

/**
 * Contents width
 * ***************/
$content-width: 1000px;
$content-width-large: 1280px;

/**
 * Color
 * ***************/
$black: #202020;
$white: #fff;
$yellow: #f0da2f;
$yellow-darker: #eed81b;
$yellow-lighter: #f0df4f;
$gray: #fafafa;
$gray-darker: #f6f6f6;
